import Vue from 'vue'
import Vuex from 'vuex'
import router, {resetRouter} from "@/router";

Vue.use(Vuex)

const store = new Vuex.Store({
    state: {
        currentPathName: '',
        paperId: '',
        closeQuestionList: false
    },
    mutations: {
        setPath (state) {
            state.currentPathName = localStorage.getItem("currentPathName")
        },
        logout() {
            // 清空缓存
            localStorage.removeItem("user")
            localStorage.removeItem("menus")
            router.push("/login")
            // 重置路由
            resetRouter()
        },
        savePaperId(state,id){
            state.paperId = id
        },
        close(state){
            state.closeQuestionList =true
        },
        open(state){
            state.closeQuestionList =false
        },
    }
})

export default store
